footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  background: #1e3a5f !important;
  color: #ffffff;
  line-height: var(--spacing);
  box-shadow: var(--shadow);
}

footer a {
  color: #ffffff !important;
}

footer a:hover {
  color: #7dd3fc !important;
}

footer .icon {
  font-size: var(--xl);
}
